import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';
import { useFormik } from 'formik';
import * as yup from 'yup';

import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider';
import Button from '@mui/material/Button';

import { Loader, TextField, SelectField } from '../components';

import { CustomerUpdate, CustomerQuery } from '../api';
import { useSnackbar } from '../hooks';
import { UpdateCustomer, CustomerTypes } from '../utils/dataTypes';
import { cleanObject } from '../utils/queryHelpers';

const validationSchema = yup.object({
  name: yup.string().required('Company name is required'),
  type: yup.string().oneOf(CustomerTypes).required('Type is required'),
  address: yup.string().required('Billing Address is required'),
  address2: yup.string(),
  city: yup.string().required('City is required'),
  state: yup.string().required('State is required'),
  zipCode: yup.string().required('Zip is required'),
  phone: yup.string().required('Phone is required'),
  fax: yup.string(),
  email: yup.string().required('Email is required'),
  website: yup.string(),
});

const initialValues = {
  id: '',
  name: undefined,
  type: undefined,
  address: undefined,
  address2: undefined,
  city: undefined,
  state: undefined,
  zipCode: undefined,
  phone: undefined,
  fax: undefined,
  email: undefined,
  website: undefined,
};

export default function EditCustomerForm() {
  const { pathname } = useLocation();
  const navigate = useNavigate();
  const queryClient = useQueryClient();
  const setSnackbarMessage = useSnackbar();

  const {
    isLoading: loading,
    isFetching: fetching,
    data,
  } = useQuery({
    queryKey: ['customer', pathname.split('/')[3]],
    queryFn: CustomerQuery,
    keepPreviousData: true,
    retry: false,
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const { mutate, isLoading } = useMutation(CustomerUpdate, {
    onSuccess: () => {
      setSnackbarMessage({
        severity: 'success',
        message: 'Saved',
      });
      queryClient.invalidateQueries(['customers']);
      navigate(-1);
    },
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const formik = useFormik<UpdateCustomer>({
    initialValues: {
      ...initialValues,
      ...cleanObject(data, Object.keys(initialValues)),
    },
    enableReinitialize: true,
    validationSchema: validationSchema,
    onSubmit: (values) => {
      mutate(values);
    },
  });

  const handleClose = () => {
    navigate(-1);
  };

  return (
    <Box sx={{ height: '100%' }}>
      <form
        onSubmit={formik.handleSubmit}
        onReset={formik.handleReset}
        style={{ height: '100%' }}
      >
        <Box sx={{ p: 3, height: '100%' }}>
          <Divider />
          <Box
            key="controls"
            sx={{
              maxHeight: 'calc(100% - 70px)',
              overflowX: 'hidden',
              overflowY: 'scroll',
            }}
          >
            <Box
              key="centerControls"
              sx={{ display: 'inline-flex', width: '100%' }}
            >
              <Box key="left" sx={{ p: 1, width: '50%' }}>
                <TextField
                  fieldName="name"
                  label="Company Name"
                  formik={formik}
                />
                <TextField
                  fieldName="address"
                  label="Billing Address Line 1"
                  placeholder="Building number and street name"
                  formik={formik}
                />
                <TextField fieldName="phone" label="Phone" formik={formik} />
                <TextField fieldName="fax" label="Fax" formik={formik} />
              </Box>
              <Box key="right" sx={{ p: 1, width: '50%' }}>
                <SelectField
                  fieldName="type"
                  label="Customer Type"
                  items={CustomerTypes}
                  formik={formik}
                />
                <TextField
                  fieldName="address2"
                  label="Billing Address Line 2"
                  placeholder="Apartment#, suite#, box#, etc"
                  formik={formik}
                />
                <TextField
                  fieldName="email"
                  label="Email"
                  placeholder="Enter Email address"
                  formik={formik}
                />
                <TextField
                  fieldName="website"
                  label="Website"
                  placeholder="Enter company`s URL"
                  formik={formik}
                />
              </Box>
            </Box>
            <Box
              key="bottomControls"
              sx={{
                marginTop: -2,
                marginBottom: 0,
                p: 1,
                paddingBottom: 0,
              }}
            >
              <TextField
                fieldName="city"
                label="City"
                placeholder="Enter city"
                formik={formik}
              />
              <TextField
                fieldName="state"
                label="State"
                placeholder="Enter state"
                formik={formik}
              />
              <TextField
                fieldName="zipCode"
                label="Zip"
                placeholder="Enter ZIP"
                formik={formik}
              />
            </Box>
          </Box>
          <Divider />
          <Box key="buttons" sx={{ paddingTop: 3, display: 'flex' }}>
            <Box key="left" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="submit"
                disabled={!formik.dirty}
              >
                Save
              </Button>
            </Box>
            <Box key="right" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleClose}
              >
                Cancel
              </Button>
            </Box>
          </Box>
        </Box>
      </form>
      <Loader visible={isLoading || loading || fetching} />
    </Box>
  );
}
